.page-mask {
  height: 250px;
  position: relative;
}

.big-text {
  font-size: 10.5vw;
  position: absolute;
  bottom: 0px;
  margin: 0px;
  color: white;
  /* color: rgb(161, 0, 32); */
}

.syntax-highlight {
  color: firebrick;
}

@media (min-width: 1100px) {
  .big-text {
    font-size: 130px;
  }
}

/* tablet */
@media (max-width: 900px) {
  .page-mask {
    height: 25vw;
  }
}

/* phone */
@media (max-width: 550px) {
  .page-mask {
    height: 130px;
  }
}
